
.App {
  width: 100%;
  min-width: 1000px;
  height: auto;
  display: grid;
  grid-template-rows: auto;
  background-color: #eee;
}
.App-header {
  width: 100%;
  min-width: 1000px;
  height: auto;
  background-color: var(--black);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:4%;
  font-size: 3vmin;
  color: var(--background)
}

.footer {
  background-color: var(--black);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5%;
  font-size: 3vmin;
  color: var(--background);
}

h1 {
  text-align: center;
}
